.advantages {
  background-color: $bg-section;
  padding-top: 39px;
  padding-bottom: 97px;

  @media screen and (min-width: 768px) {
    padding-top: 66px;
    padding-bottom: 140px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 39px;
    padding-bottom: 139px;
  }

  &__list {
    @media screen and (min-width: 768px) {
      display: flex;
      margin-left: -60px;
    }
  }

  &__item {
    margin-bottom: 38px;

    &::before {
      content: '';
      display: block;
      height: 50px;
      width: 50px;
      background-repeat: no-repeat;
      background-size: contain;
      margin-bottom: 16px;

      @media screen and (min-width: 768px) {
        margin-bottom: 36px;
      }

      @media screen and (min-width: 1280px) {
        margin-bottom: 40px;
      }
    }

    @media screen and (max-width: 767px) {
      &:last-child {
        margin-bottom: 0;
      }
    }

    @media screen and (min-width: 768px) {
      flex-basis: calc(100% / 3 - 60px);
      margin-left: 60px;
    }

    &:nth-child(1)::before {
      background-image: url('../images/desktop/advantages/icon1.png');

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url('../images/desktop/advantages/icon1@2x.png');
      }
    }

    &:nth-child(2)::before {
      background-image: url('../images/desktop/advantages/icon2.png');

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url('../images/desktop/advantages/icon2@2x.png');
      }
    }

    &:nth-child(3)::before {
      background-image: url('../images/desktop/advantages/icon3.png');

      @media screen and (min-device-pixel-ratio: 2),
        screen and (min-resolution: 192dpi),
        screen and (min-resolution: 2dppx) {
        background-image: url('../images/desktop/advantages/icon3@2x.png');
      }
    }
  }

  &__content {
    font-family: $title-font;
    font-weight: 400;
    font-size: 36px;
    line-height: 1.15;
    color: $accent-color;

    @media screen and (min-width: 768px) {
      font-size: 48px;
      line-height: 1.15;
    }

    @media screen and (min-width: 1280px) {
      font-size: 58px;
      line-height: 1.15;
    }
  }

  &__description {
    font-family: $main-font;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.7;
    color: $head-text;
    margin-top: 16px;

    @media screen and (min-width: 768px) {
      font-size: 14px;
      line-height: 1.74;
    }
    @media screen and (min-width: 1200px) {
      font-size: 16px;
      line-height: 1.76;
    }
  }
}
